:root{--spacing: 8px;--vertical-component-spacing: calc(var(--spacing)* 25)
}:root{--iphone-width: 375px;--iphone-component-width: 295px;--component-max-width: 700px;--body-font-size: 16px}:root{--column-1: 46%;--column-2: 46%;--column-3: 8%}html{margin:0;padding:0}body{background-color:black;color:white;margin:0;padding:0}a{text-decoration:none;color:inherit}h1{margin:0;padding:0}h4{margin:0;padding:0}img{margin:0;padding:0}

.grid{display:grid;height:100vh;width:100%;grid-template-columns:var(--column-1) var(--column-2) var(--column-3);grid-template-rows:auto 1fr 1fr 1fr 1fr 1fr 1fr;grid-template-areas:"hd hd hd" "tp tp tp" "about about soc" "sp sp soc" "exp exp soc" "ft ft ft"}@media screen and (max-width: 375px){.grid{grid-template-columns:100%;grid-template-rows:auto 1fr 1fr 1fr 1fr 1fr 1fr 1fr;grid-template-areas:"hd" "tp" "about" "sp" "exp" "soc" "ft"}}.headerComponent{grid-area:hd}.titleComponent{grid-area:tp}.aboutComponent{grid-area:about}.spotifyProjectComponent{grid-area:sp}.experienceComponent{grid-area:exp}.socialsComponent{grid-area:soc}.footerComponent{grid-area:ft}


.nav{text-align:right;margin-right:calc(var(--spacing) * 5);margin-top:calc(var(--spacing) * 4);margin-bottom:calc(var(--spacing) * 4)}.nav .item{list-style-type:none;margin:0;padding:0;font-family:'Roboto Condensed', sans-serif;font-size:16px;color:#FFFFFF;letter-spacing:0.25px;text-align:right;line-height:calc(16px + 4px);letter-spacing:.5px;opacity:0.87;color:#DEDEDE}.nav .item li{padding:calc(var(--spacing) * 2);display:inline;position:relative}.nav .item a{color:#fff;display:inline-block;padding:var(--spacing);position:relative}.nav .item a:after{background:none repeat scroll 0 0 transparent;bottom:0;content:"";display:block;height:2px;left:50%;position:absolute;background:#BC6FF1;transition:width 0.3s ease 0s, left 0.3s ease 0s;width:0}.nav .item a:hover:after{width:100%;left:0}

