.aboutComponent{margin-top:var(--vertical-component-spacing);max-width:calc(300px + calc(var(--spacing) * 15));display:block;margin-left:auto;margin-right:auto}@media screen and (max-width: 375px){.aboutComponent{max-width:var(--iphone-component-width)}}.rollOut{transform:translateY(calc((1em + 6px) * 3 + var(--spacing) * 3 * 3))}

.container{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:auto}.card{position:relative;background-color:#111111;padding:calc(var(--spacing) * 3);border-radius:5px}.text{font-family:'Roboto Condensed', sans-serif;font-size:var(--body-font-size);color:#FFFFFF;letter-spacing:0.25px;text-align:left;line-height:calc(var(--body-font-size) + 10px);letter-spacing:.5px;opacity:0.87;white-space:pre-line}

