.title{font-family:'Roboto Condensed', sans-serif;font-size:14px;color:#FFFFFF;letter-spacing:0.25px;text-align:left;line-height:calc(14px + 6px);letter-spacing:2px;opacity:0.87;margin-bottom:calc(var(--spacing) * 2);text-transform:uppercase}.underline{height:1px;background-color:#BC6FF1}

.socialsComponent{position:relative;margin-top:var(--vertical-component-spacing)}.logoContainer{position:-webkit-sticky;position:sticky;top:50%;transform:translateY(-50%);width:32px;flex-direction:column}@media screen and (max-width: 375px){.logoContainer{flex-direction:row;width:70%;margin-left:15%;display:flex;justify-content:space-between}}.logo{height:32px;width:32px;margin-bottom:32px;filter:invert(58%) sepia(28%) saturate(4492%) hue-rotate(231deg) brightness(95%) contrast(99%)}.linkedin{transition:0.3s}.linkedin:hover{filter:invert(26%) sepia(97%) saturate(1507%) hue-rotate(197deg) brightness(94%) contrast(88%)}.github{transition:0.3s}.github:hover{filter:invert(100%) sepia(0%) saturate(7491%) hue-rotate(57deg) brightness(117%) contrast(101%)}.lastfm{transition:0.3s}.lastfm:hover{filter:invert(13%) sepia(58%) saturate(5470%) hue-rotate(354deg) brightness(96%) contrast(117%)}.stackoverflow{transition:0.3s}.stackoverflow:hover{filter:invert(100%) sepia(0%) saturate(7491%) hue-rotate(57deg) brightness(117%) contrast(101%)}

