e-height:calc(14px + 8px);letter-spacing:2px;opacity:0.87;height:40px;width:100px;margin-right:calc(var(--spacing) * 2);display:flex;justify-content:center;align-items:center}}.jobContainer{background-color:#171717;border-radius:5px;margin-left:calc(var(--spacing) * 3);padding:calc(var(--spacing) * 3)}@media screen and (max-width: 375px){.jobContainer{margin-left:0}}.jobTitleContainer{background-color:#1F1F1F;padding:calc(var(--spacing) * 1) calc(var(--spacing) * 2);border-radius:5px}.jobTitle{font-family:'Roboto Condensed', sans-serif;font-size:18px;color:#FFFFFF;letter-spacing:0.25px;text-align:left;line-height:calc(18px + 8px);letter-spacing:1px;opacity:0.87;margin-bottom:calc(var(--spacing) * 1)}.jobDuration{font-family:'Roboto Condensed', sans-serif;font-size:14px;color:#FFFFFF;letter-spacing:0.25px;text-align:left;line-height:calc(14px + 8px);letter-spacing:1px;opacity:0.87}.jobDescription{font-family:'Roboto Condensed', sans-serif;font-size:var(--body-font-size);color:#FFFFFF;letter-spacing:0.25px;text-align:left;line-height:calc(var(--body-font-size) + 8px);letter-spacing:.5px;opacity:0.87;background-color:#1F1F1F;margin-top:calc(var(--spacing) * 1);padding:calc(var(--spacing) * 1) calc(var(--spacing) * 2);border-radius:5px}

